import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import store from '../store/store.jsx';

let active= false;


class UsersList extends React.Component {
  constructor(props){
    super(props);

    this.toggleUser = this.toggleUser.bind(this);
  }
  componentDidMount () {
  }
  render(){
      let {users} = this.props;
      return (

          <div className='users-list'>
              <ul className="users-ul">
                  {users.map((user, key) =>

                      <li key={key}
                          onClick={this.toggleUser}
                          data-active={false}
                        >
                          <img src={user.avatar} className="avatar-pic" />
                          {user.name}
                      </li>
                  )}
              </ul>
          </div>
      );
    }

    /* FUNCTIONS */
    toggleUser (e) {
        let listItem = e.currentTarget;
        let activeListItem = document.querySelector('.user-active');
        let isActive = listItem.dataset['active'];
        if (activeListItem) {
          activeListItem.dataset.active = false;
          activeListItem.className = '';
        }
        listItem.dataset.active = true;
        listItem.className = 'user-active';
        if (isActive == "true") {
          listItem.dataset.active = false;
          listItem.className = '';
        }
    }
};



UsersList.propTypes = {
    users: PropTypes.array.isRequired
};

const mapStateToProps = function(store) {
    return {
        users:  store.UserStatus.users
    };
};

export default connect(mapStateToProps)(UsersList);

